<template>
  <!--  组织管理-->
  <div class="management">
    <div class="nav-form">
      <el-form ref="form" class="form" :model="form" label-width="80px" :inline="true">
        <el-form-item label="组织名称">
          <el-input placeholder="请输入内容" v-model="form.deptName"></el-input>
        </el-form-item>
        <el-form-item label="组织状态">
          <el-select v-model="form.region">
            <el-option :label="item.label" :value="item.value" v-for="item in stats"></el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="find()">搜索</el-button>
        <el-button @click="clear()">重置</el-button>

      </el-form>
      <div class="add">
        <el-button type="primary" @click="showForm('新增')">新增</el-button>
      </div>
    </div>
    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form ref="addForm" :model="addForm" :rules="rules">
        <el-form-item label="部门名称" :label-width="formLabelWidth" prop="deptName">
          <el-input class="input" v-model="addForm.deptName" autocomplete="off" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="显示顺序" :label-width="formLabelWidth" prop="orderNum">
          <el-input type="number" class="input" v-model="addForm.orderNum" autocomplete="off"
                    placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="上级部门" :label-width="formLabelWidth" prop="parentId">

<!--          <my-tree style="width: 100%" :options="formList" @getValue="getSelectedValue"></my-tree>-->
          <my-el-tree  style="width: 100%" :options="formList" :value=row.parentId  :accordion="true" @getValue="getSelectedValue"/>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-radio-group v-model="addForm.status">
            <el-radio :label="0">启用</el-radio>
            <el-radio :label="1">停用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('addForm')">确 定</el-button>
      </div>
    </el-dialog>
    <el-tree
      @node-click="handleNodeClick"
      :data="formList"
      ref="tree"
      :default-expanded-keys="defaulExpanded"
      :expand-on-click-node="false"
      node-key="deptId"
      :props="defaultProps"
      accordion>
      <div class="custom-tree-node" slot-scope="{node, data}">
        <div class="tree">
          <a class="detpName">  {{ data.deptName }} </a>
          <div>{{data.createTime}}</div>
          <div class="item-r">
            <el-link type="primary" class="link" @click="currentShow(0)">新增下一级</el-link>
            <el-link type="primary" class="link" @click="currentShow(1)">编辑</el-link>
            <el-link type="primary" class="link" @click="currentShow(2)">删除</el-link>
          </div>
        </div>
      </div>
    </el-tree>
  </div>
</template>

<script>
import {
  addOrganize,
  delOrganizeList,
  getOrganizeList,
  upDataOrganize
} from "@/newwork/system-colltroner";
import {fn, getNowFormatDate, treeToArray} from "@/uti";
import { isValidNumber } from "@/util/validate";
import myTree from "@/components/myTree";
import myFooter from "@/components/myFooter";
import myElTree from "@/components/my-el-tree";

export default {
  name: "serve-manage",
  data: function() {
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip地址输入有误,请确认"));
      } else {
        callback();
      }
    };
    return {
      title: "新增",
      dialogFormVisible: false,
      dialogFormVisibles: false,
      formLabelWidth: "120px",
      form: {},
      defaulExpanded: [],
      addForm: {
        deptName: "",  //部门名字
        orderNum: "", //显示顺序
        parentId: "", //父级单位
        status: 0 //状态
      },
      clickCount: 0,

      parentName: "", //父级单位
      row: {},  //获取当前 点击的那一行
      formatList: [],
      stats: [
        { label: "启用", value: "启用" },
        { label: "禁用", value: "禁用" }
      ],
      defaultProps: {
        children: "children",
        label: "deptName"
      },
      currentNodeKey: '',
      showTitle: ['新增','编辑', '删除'],
      showIndex: 0,
      list: [], //未被格式化 以前的列表
      rules: {
        deptName: [
          { required: true, message: "此项为必填项， 请确认", trigger: "blur" }
        ],
        orderNum: [
          { required: true, message: "此项为必填项， 请确认", trigger: "blur" },
          { validator: validateNum, message: "请输入合法的数字", trigger: "blur" }
        ],
        ancestors: [
          { required: true, message: "此项为必填项， 请确认", trigger: "blur" }
        ],
        parentId: [
          { required: true, message: "此项为必填项， 请确认", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    getOrganizeList(form) {
      getOrganizeList(form).then(res => {
        console.log(res);
        this.$emit('total', res.data.data.total)
        this.list = res.data.data
      }).catch(e => {
        this.$message.error(e)
      });
    },
    find(){
      this.getOrganizeList(this.form)
    },
    clear(){
      if(Object.keys(this.form).length !==0){
        this.form = this.$options.data().form
        this.getOrganizeList(this.form)
      }
    },
    handleNodeClick(data) {
      this.row = data;
      this.showForm(this.showTitle[this.showIndex]);
    },
    showForm(type) {
      if (type === "编辑") {

        this.dialogFormVisible = true;
        this.title = type;
        // this.addForm = this.$options.data().addForm;
        this.addForm = this.row;
      } else if(type === '新增') {
        this.dialogFormVisible = true;
        this.title = type;
        this.addForm = this.$options.data().addForm;
        this.addForm.parentId = this.row.deptId;
        this.$nextTick(() => {
          this.$refs.myTree.valueName = this.row.deptName
        })
      }else {
        this.removeIt()
        this.showIndex = 0
        console.log('--------');
      }
    },
    currentShow(index) {
      this.showIndex = index;
    },
    removeIt() {
      delOrganizeList(this.row.deptId).then(res => {
        if (res.data.code === 200) {

          this.defaulExpanded.push(this.row.parentId)
          this.getOrganizeList(this.form);
          this.$message.success("提交完成");
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    getSelectedValue(value){
      this.addForm.parentId = value
    },
    submitForm(addForm) {
      this.$refs[addForm].validate((valid) => {
        if (valid) {
         this.title === '新增' ? addOrganize(this.addForm).then(res => {
            if(res.data.code === 200){
              this.dialogFormVisible = false
              this.getOrganizeList(this.form)
              this.$message.success('提交完成')
              // this.getOrganizeList(this.form)
            }else {
              this.$message.error(res.data.msg)
            }
          }).catch(e => {
            this.$message.error(e)
          }) : upDataOrganize(this.addForm).then(res => {
           if(res.data.code === 200){
             this.dialogFormVisible = false
             this.getOrganizeList(this.form)
             this.defaulExpanded.push(this.row.deptId)
             this.$message.success('提交完成')
           }else {
             this.$message.error(res.data.msg)
           }
         })
        } else {
          this.$message({
            message: "提交失败, 请重试",
            type: "error"
          });
        }
      });
    }
  },
  components: {
    myTree,
    myFooter,
    myElTree
  },
  created() {
    this.getOrganizeList(this.form);
  },
  computed: {
    createTime() {
      return this.addForm.createTime = getNowFormatDate();
    },
    formList(){
      return fn(this.list)
    },
    parentId(){
      return parseInt(this.row.parentId)
    }
  },
};
</script>

<style>
.management{
  width: 100%;
  padding: 20px;
  margin-left: 20px;
  margin-top: 20px;
  box-shadow: 0 0 15px #ccc;
  background-color: #fff;
  border-radius: 10px;
  height: 80vh;
}
.input {
  width: 30%;
}

.operate a {
  margin: 0 10px;
}

.custom-tree-node {
  width: 100%;
}

.tree {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tree .link {
  margin-right: 20px;
}

.form-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tree .detpName {
  width: 200px;
}
.nav-form{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin: 10px 0;
  min-width: 900px;
}
.form {
  height: 40px;
}
.add{
  text-align: right;
}
</style>
